<template>
  <div dir="rtl">
    <v-toolbar flat color="grey lighten-2">
      <v-toolbar-title>جدول الخصومات</v-toolbar-title>
      <v-divider
        class="mx-2"
        inset
        vertical
      ></v-divider>
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" max-width="500px">
        <template v-slot:activator="{ on }">
          <v-btn color="blue" flat class="mb-2" v-on="on">خدمه جديده</v-btn>
        </template>
        <v-card>
          <v-card-title>
            <span class="headline cairo">{{ formTitle }}</span>
          </v-card-title>

          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.name" label="اسم الخدمه"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.beforDiscount" label="القيمه قبل الخصم"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.afterDiscount" label="القيمه بعد الخصم"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.discountRate" label="نسبه الخصم"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="red darken-1" flat @click="close">Cancel</v-btn>
            <v-btn color="success darken-1" flat @click="save">Save</v-btn>
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-data-table
      :headers="headers"
      :items="services"
      class="elevation-1 text-xs-right ma-2 grey lighten-2"
    >
      <template v-slot:items="props">
        <td class="text-xs-left">{{ props.item.name }}</td>
        <td class="text-xs-left">{{ props.item.beforDiscount }}</td>
        <td class="text-xs-left">{{ props.item.afterDiscount }}</td>
        <td class="text-xs-left">{{ props.item.discountRate }}</td>
        <td class="text-xs-left">
          <Rate v-bind:service="props"/>
        </td>
        <td class="text-xs-left">
          <v-icon
            small
            class="ma-2"
            @click="editItem(props.item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            class="ma-2"
            @click="deleteItem(props.item)"
          >
            delete
          </v-icon>
        </td>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize">Reset</v-btn>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import Rate from '../../components/Rate'
  export default {
    components: {
      Rate
    },
    data: () => ({
      dialog: false,
      headers: [
        {
          text: 'الخدمات',
          align: 'left',
          sortable: false,
          value: 'name'
        },
        { text: 'قبل الخصم', value: 'beforDiscount' },
        { text: 'بعد الخصم', value: 'afterDiscount' },
        { text: 'نسبه الخصم', value: 'discountRate' },
        { text: 'التقيم', value: 'rate', sortable: false },
        { text: 'الضبط', value: 'name', sortable: false }
      ],
      services: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        beforDiscount: '',
        afterDiscount: '',
        discountRate: ''
      },
      defaultItem: {
        name: '',
        beforDiscount: '',
        afterDiscount: '',
        discountRate: ''
      }
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'اضافه خدمه' : 'تعديل خدمه'
      },
      discount () {
        return this.beforDiscount-this.afterDiscount
      }
    },

    watch: {
      dialog (val) {
        val || this.close()
      }
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.services = [
          {
            name: 'خلع ضرس',
            beforDiscount: 159,
            afterDiscount: 6.0,
            discountRate: 4.0
          },
          {
            name: 'تركيب تقويم',
            beforDiscount: 237,
            afterDiscount: 9.0,
            discountRate: 4.3
          },
          {
            name: 'فحص سكري',
            beforDiscount: 262,
            afterDiscount: 16.0,
            discountRate: 6.0
          },
        ]
      },

      editItem (item) {
        this.editedIndex = this.services.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        const index = this.services.indexOf(item)
        confirm('Are you sure you want to delete this item?') && this.services.splice(index, 1)
      },

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.services[this.editedIndex], this.editedItem)
        } else {
          this.services.push(this.editedItem)
        }
        this.close()
      }
    }
  }
</script>