<template>
    <v-dialog
    v-model="dialog"
    max-width="500px"
    >
        <v-btn
        small
        flat
        color="orange"
        class="ma-2"
        slot="activator"
        >
        تقيم
        </v-btn>
        <v-card fill-height>
            <v-card flat justify-center align-center>
                <v-card-text>
                    <v-spacer></v-spacer>
                    <v-icon color="orange" @click="rate">star_border</v-icon>
                    <v-icon color="orange" @click="rate">star_border</v-icon>
                    <v-icon color="orange" @click="rate">star_border</v-icon>
                    <v-icon color="orange" @click="rate">star_border</v-icon>
                    <v-icon color="orange" @click="rate">star_border</v-icon>
                    <v-spacer></v-spacer>
                </v-card-text>
            </v-card>
        </v-card>
    </v-dialog>
    
</template>
<script>
export default {
    name: 'Rate',
    props: ["service","center"],
    data(){
        return {
            dialog: false,
            service_id: 2,
            center_id: 4,
            service_rate_counter: 1,
            center_rate_counter: 1,
            ratedService: [],
            ratedCenter: []
        }
    },
    methods: {
        rate(){
            this.ratedService = {
                id: this.service_id,
                rate: this.service_rate_counter,
                service_name: this.service.item.name,
                service_afterDiscount: this.service.item.afterDiscount,
                service_beforDiscount: this.service.item.beforDiscount,
                service_discountRate: this.service.item.discountRate,
            }
            // console.log(this.ratedService)
            this.ratedCenter = {
                id: this.center_id,
                rate: this.center_rate_counter,
            }
            // console.log(this.ratedCenter)
        }
    }
}
</script>